<template>
    <router-view></router-view>
</template>

<script>
    export default {
        name: "management",
        created(){
            if(!localStorage.getItem('userID')){
            	this.$router.push({path:'/login',query:{title:'后台管理员登录'}});
            }
        },
    }
</script>

